@import url(https://fonts.googleapis.com/css?family=Patrick+Hand);
@import url(https://fonts.googleapis.com/css?family=Roboto);
body {
  margin: 0;
}

.layout {
  box-sizing: border-box;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  background: -webkit-linear-gradient(45deg, #0d2843 155px, #2980b9);
  background: linear-gradient(45deg, #0d2843 155px, #2980b9);
}

.container {
  background: white;
  width: 300px;
  min-height: 50vh;
  margin: auto;
  border-radius: 10px;
  padding: 15px;
  position: absolute;
}

header {
  text-align: center;
}

section {
  font-size: 1.25rem;
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  justify-content: space-between;
}

.list-container {
  background: #f9f9f9;
  display: flex;
  flex-direction: column;
  margin: 0.5rem;
}

ul {
  margin: 0;
  padding: 0;
}

.styleEl {
  width: 436px;
  position: relative;
  height: 436px;
  background: #ffcc00;
  border-radius: 29% 71% 22% 78% / 57% 72% 28% 43%;
}

h2 {
  font-family: 'Patrick Hand', cursive;
  font-size: 35px;
  margin-top: 10px;
  margin-bottom: 10px;
}

.ToDoList {
  display: flex;
  justify-content: center;
  align-items: center;
}

.done {
  color: #0080bc91;
  text-decoration: line-through;
}

.deleteButton {
  background: #006194;
  color: white;
  margin-left: auto;
  border: 0;
  border-radius: 5px;
}

.deleteButton:hover {
  background: #ececec;
  color: #006194;
}

input[type='text'] {
  height: 20px;
  width: 100%;
  font-family: 'Roboto';
  margin-bottom: 20px;
}

.listItem {
  display: flex;
  align-items: flex-end;
  font-family: 'Roboto';
  font-size: 14px;
  width: 290px;
  background: #ffcc00;
  margin-bottom: 10px;
  padding: 5px;
  border-radius: 5px;
}

.listItem:nth-child(even) {
  background: #ebe1d3;
}

